<template>
  <v-container min-height="100%">
      <h1 class="text-left text-capitalize">{{$route.params.companyName.replace(/_/gi, " ").replace(/-/gi, " ")}}</h1>
      <hr>
      <v-container class="pa-5">
      <v-expansion-panels popout>
          <v-expansion-panel v-for="category in documents" :key="(category.name)">
              <v-expansion-panel-header>{{category.name}}</v-expansion-panel-header>
              <v-expansion-panel-content>
                  <a v-for="(file, index) in category.files" :key="index" @click="getFile(file.key)">
                      <p>{{file.name}}</p>
                  </a>
              </v-expansion-panel-content>
          </v-expansion-panel>
      </v-expansion-panels>
      </v-container>
  </v-container>
</template>

<script>
import CompanyService from '../service/CompanyService';
export default {
    name: "Company",
    data: () => ({
        documents: [],
        error: ''
    }),
    async created() {
        try{
            this.documents = await CompanyService.getCompanyDocuments(this.$route.params.companyName);
        } catch(err){
            this.error = err.message;
        }
    },
    methods: {
        getFile(key){
            CompanyService.getCompanyDocByKey(key);
        }
    }
}
</script>

